@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&family=Montserrat:wght@400;500;600;700&display=swap);
body{margin:0;font-family:'Montserrat', sans-serif;background-color:#111616;color:#FFF8E8;line-height:2rem}.welcomeText{font-size:2rem;font-weight:600;margin-top:200px;text-align:center}.primaryButton{border:none;outline:none;cursor:pointer;font-size:1rem;font-weight:600;padding:10px 30px 10px 30px;border-radius:0.25rem}.primaryButton:hover,.primaryButton:active{box-shadow:0 1px 4px rgba(6,141,157,0.1),0 4px 12px rgba(6,141,157,0.06)}.next{background-color:#4F5492;transition:background-color 0.2s cubic-bezier(0.4, 0, 0.2, 1),color 0.2s cubic-bezier(0.4, 0, 0.2, 1),box-shadow 0.2s cubic-bezier(0.4, 0, 0.2, 1)}.cancel{background-color:#f44336;transition:background-color 0.2s cubic-bezier(0.4, 0, 0.2, 1),color 0.2s cubic-bezier(0.4, 0, 0.2, 1),box-shadow 0.2s cubic-bezier(0.4, 0, 0.2, 1)}.next:hover{background-color:#6065A9}.next:active{background-color:rgba(13,163,182,0.16)}.cancel:hover{background-color:#ffcdd2}.cancel:active{background-color:#ef9a9a}.mainContainer{margin-left:35vw;margin-top:0}.videoContainer{width:35vw;position:absolute;z-index:99;background-color:#000;height:calc(50% - 35px)}.userVideoContainer{top:calc(50% + 35px);right:default;width:35vw;height:calc(50% - 35px);z-index:default}.userVideoFull{position:absolute;top:40px;right:40px;width:200px;height:200px}.partnerVideoContainer{top:70px}.partnerVideoFull{height:100%;width:100vw;top:0}.video{width:100%;height:100%;object-fit:cover;box-shadow:0 1px 4px rgba(6,141,157,0.1),0 4px 12px rgba(6,141,157,0.06)}.controlsContainer{position:absolute;top:calc(100% - 100px);align-items:center;justify-content:space-around;height:100px;z-index:999;width:35vw;display:flex}.controlsFull{width:100vw}.iconContainer{padding:12px;border-radius:50%;border-width:4px;border-style:solid;border-color:#068D9D;width:2rem;height:2rem;cursor:pointer;background-color:transparent;box-shadow:0 1px 4px rgba(6,141,157,0.1),0 4px 12px rgba(6,141,157,0.06);transition:background-color 0.2s cubic-bezier(0.4, 0, 0.2, 1)}.iconContainer:hover{background-color:rgba(13,163,182,0.16)}.iconContainer svg{width:100%;height:auto}.iconBasic{color:#06A0B1}.iconAlternative{color:#f44336}.inputContainer{bottom:0;width:100%;z-index:2;height:70px}.chatInput{width:calc(100% - 220px);border-width:4px;border-style:solid;border-color:#068D9D;padding:13px 80px 12px 12px;font-size:2rem;outline:none;height:100%;background-color:#111616;color:#FFF8E8}.chatInput::-webkit-input-placeholder{color:rgba(255,248,232,0.8)}.chatInput:-ms-input-placeholder{color:rgba(255,248,232,0.8)}.chatInput::-ms-input-placeholder{color:rgba(255,248,232,0.8)}.chatInput::placeholder{color:rgba(255,248,232,0.8)}.attachmentButton{position:absolute;right:140px;z-index:3;margin-top:18px;font-size:2rem;color:#068D9D;cursor:pointer}.chatButton{border:none;width:120px;padding:17px 0 16px 0;cursor:pointer;background:#068D9D;outline:none;color:#FFF8E8;font-size:2rem}.chatButton:hover{background:#06A0B1}@media screen and (max-width: 860px){html{font-size:16px}.mainContainer{margin-left:0px;margin-top:50vh}.videoContainer{width:100%;z-index:99;height:50%}.videoContainer{width:100%;height:50%}.userVideoContainer{top:2%;right:2%;width:80px;height:80px;z-index:100}.userVideoFull{width:100px;height:100px}.partnerVideoContainer{top:0}.partnerVideoFull{height:100%;left:0}.controlsContainer{top:calc(50% - 60px);height:50px;z-index:999;width:100%}.controlsFull{top:auto;top:initial;bottom:5%}.iconContainer{cursor:none;padding:10px;border-radius:50%;border-width:2px;width:1.5rem;height:1.5rem}.welcomeText{margin-top:60vh}.chatInput{width:calc(100% - 116px);border-width:3px;border-color:#068D9D;padding:8px 42px 8px 8px;font-size:1rem}.inputContainer{height:40px}.attachmentButton{right:70px;margin-top:8px;font-size:1.5rem}.chatButton{width:60px;padding:11px 0 11px 0;font-size:1rem}}

.headerWrapper{max-width:calc(60rem + 1rem * 2);margin:0 auto;height:70px}.headerContainer{justify-content:space-between;align-items:center;height:70px;display:flex}.headerLogo{align-items:center;font-size:2rem;width:100%;justify-content:space-between;display:flex;flex-direction:row;padding-left:100px;padding-right:100px}.logoText{font-family:'Great Vibes', cursive;margin-left:0.6em;font-weight:500;font-size:3rem;color:#068D9D;flex:50% 1}.onlineText{display:flow-root;text-align:right;flex:50% 1}.onlineIcon{color:#068D9D;font-size:1.8rem;margin-bottom:-2px}.logoText:before{content:"";display:block;height:0;width:0;margin-top:-0.2075em}.logoText:after{content:"";display:block;height:0;width:0;margin-bottom:-0.2075em}.dropShadow{box-shadow:0 1px 6px rgba(6,141,157,0.1),0 4px 24px rgba(6,141,157,0.06)}@media screen and (max-width: 860px){.onlineText{display:none}.logoImg{flex-shrink:0;height:1.1em}.headerLogo{margin-left:25px}.dropShadow{display:none}.headerWrapper{height:30px}.headerContainer{height:30px}}

.chatBox{height:calc(100vh - 150px);overflow:auto;display:flex;flex-direction:column;padding-bottom:10px}@media screen and (max-width: 860px){.chatBox{height:calc(50vh - 45px);padding-bottom:5px}}

.messageContainer{width:100%;display:flex}.message{font-size:1.5rem;border-radius:5px;border-width:2px;border-style:solid;padding:4px 8px 4px 8px;margin:6px 8px 0px 8px;word-wrap:break-word;max-width:50%}.yourMessage{background-color:#06A0B1;border-color:#068D9D;margin-left:auto}.partnerMessage{background-color:#565B9F;border-color:#4F5492}.systemMessage{background-color:gray;border-color:black}@media screen and (max-width: 860px){.message{font-size:1rem}}

.loading{height:calc(100vh - 70px);width:calc(65vw);font-size:6rem;display:flex;justify-content:center;align-items:center;text-align:center;position:absolute;background-color:transparent;z-index:4;font-family:"Great Vibes", cursive;font-weight:500;color:#068D9D}@media screen and (max-width: 860px){.loading{height:calc(50vh);width:100%}}

.lockedContainer{font-size:3rem;height:calc(100vh - 70px);display:flex;justify-content:center;align-items:center;text-align:center}@media screen and (max-width: 860px){.lockedContainer{height:50vh}}

